.container {
  margin: 10%;
}

h1 {
  margin-bottom: 16px
}

.form-input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 4px 0;
}

.submit {
  background-color: #4CAF50;
  color: white;
  padding: 6px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.label {
  margin-bottom: 5px;
}

.result {
  font-size: 36px;
  text-align: center;
  padding: 100% 0;
}


.head {
  padding: 0 16px;
  background-color: var(--primary-color);
  ;
  box-sizing: border-box;
  height: 44px;
  text-align: right;

  button {
    position: relative;
    background-color: transparent;
    margin: 0;
    padding: 0;
    border: none;
    color: #ffffff;
    height: 44px;
    padding-left: 18px;
    font-size: 1rem;

    svg {
      position: absolute;
      top: 50%;
      left: 0;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
    }
  }
}
